
#scroll-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  -webkit-transition: opacity 0.5s, background 0.3s;
  transition: opacity 0.5s, background 0.3s;
  display: none;
  opacity: 0;
}
#scroll-top:hover {
    background: #78bff8;
}



